main {
    background-image: url('../assets/IMG_9166.jpg');
    background-repeat: no-repeat; 
    background-size: cover;
}
h1 {
    color: brown;
    font-size: 2em;
    text-align: center;
}
h2,legend {
    font-size: 1.3em;
}
div,p,label,select,option {
    font-size: 1em;
}
.cboxes {
    height: 0.6em;
    width: 0.6em;
}
input {
    font-size: 1.2em;
}
.option-box{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 75vw;
}
.flex-box {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center; 
    align-items: center;     
}
/*It is used for invalid statement*/
.invalid{
    color: red;
}
table {
    border-collapse: collapse;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    width: 100%;
}
td {
    border: 2px solid blue;
    font-size: 1em;
    width: 33vw;
} 
th {
    border: 2px solid blue;
    font-size: 1.2em;
    width: 33vw;
} 
/*Only single row has white background*/
tr:nth-child(odd) {
    background-color: white;
}
.flexitem{
    flex:1 1 auto;/*grow shrink basis*/
}